.hide {
  display: none;
}

.more,
.write-us{
  font-family: "Gilroy", "Open Sans",  "Lucida Sans", sans-serif, serif;
  font-weight: 800;
  line-height: 1em;
  font-size: 1.2em;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  display: inline-block;
  width: 220px;
  height: 40px;
  text-align: center;
  position: relative;
  padding-top: 12px;
  z-index: 1;
}

.more::before,
.write-us::before {
  content: "";
  display: block;
  width: 220px;
  height: 8px;
  background-color: $bgc-yellow-saturated;
  position: absolute;
  top: 17px;
  left: -2px;
  z-index: -1;
}

.more:hover,
.write-us:hover {
  background-color: $bgc-yellow-saturated;

  &::before {
    display: none;
  }
}

.more:active,
.write-us:active {
  color: rgba(0, 0, 0, 0.3);

  &::before {
    display: none;
  }
}